.header-page {
    width: 100%;
    background-color: var(--main-background-color);
    filter: drop-shadow(0px 4px 24px rgba(105, 105, 105, 0.1));
}


.main-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include tablet {
   
    }

    @include desktop {

    }
}
.nav {
    display: none;

    @include tablet {
    display: flex;
    margin-left: 191px;
    gap: 28px;
    }

    @include desktop {
    margin-left: 735px;
    display: flex;
    gap: 28px;
    }
}
.nav__link {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: calc(16/14);  
    color: #212121;

    &:hover,
    &:focus {
    color: #BBF330;
    }
}
.nav__link--current {
    color: #BBF330;
}
.burger-btn {
    width: 40px;
    height: 40px;
    background-color: #fff;
    border: none;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    @include tablet {
        display: none;
    }

    @include desktop {
        display: none;
    }
}
.burger-btn__icon { 
    width: 28px;
    height: 28px;
}
.nav-button {
    display: none;

    @include tablet {
    display: flex;
    background: #BBF330;
    border-radius: 30px;
    margin-left: auto;
    height: 32px;
    width: 98px;
    border: none;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    line-height: calc(20/13);
    }

    @include desktop {
    background: #BBF330;
    border-radius: 30px;

    }
}